body {
  margin: 0;
  background: #222;
  font-family: "Ubuntu", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow-x: hidden;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.intro{
	color: #fff;
	padding: 120px 20px 30px 14px;
	font-size: 50px;
}

.intro p{
	display: inline-block;
}

@media screen and (max-width: 800px) {

  .intro{
	color: #fff;
	padding: 100px 20px 30px 20px;
	font-size: 50px;
  }

   .intro p{
	 display: block;
  }

}

.navlink {
	color: #999;
}

.navlink:hover{
	color:white;
	text-decoration: none;
}

.navbar-trans{
	background: transparent;
	border-color: transparent;
}

.navbar-trans-lg{
	min-height: 100px;
	background: transparent;
	border-color: transparent;
	transition: 0.4s;
}

.navbar-trans-xs{
	min-height: 30px;
	transition: 0.4s;
}

/*
.navbar-collapse-xs{
	background: red;
	border-color: transparent;
} */

.navbrand-font-lg {
	padding-top: 40px;
	font-size: 30px;
	transition: 0.4s;
}

.navbrand-font-xs {
	padding-top: 15px;
	font-size: 20px;
	transition: 0.4s;
}

.navbar-right-lg{
	padding-top: 25px;
	transition: 0.4s;
}

.navbar-right-xs{
	transition: 0.4s;
}

.contact{
	background: black;
	color: white;
	text-align: center;
	padding-bottom: 10px;
	padding-top: 20px; 
	margin-top: 0px;
	margin-bottom: -20px;
}

.contact-link{
	font-size: 40px;
	margin-bottom: -10px;
}

.contact-link a{
	color: white;
	padding-left: 5px;
}

.contact-link a:hover{
	color: red;
	transition: 0.5s;
}

.contact-icons {
	font-size: 27px;
}

.contact-icons a{
	color: white;
	padding-left: 5px;
}

.contact-icons a:hover{
	color: red;
	transition: 0.5s;
}

.icons {
	font-size: 27px;
}

.icons a{
	color: black;
}

.icons a:hover{
	color: red;
	transition: 0.5s;
}

